
import axios from 'axios';
/*import Xheader from '@/components/comother/rheader'*/
import Vue from 'vue';
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

Vue.component(Popup.name, Popup);
export default {
data() {
return {
contents: { rights: '评分说明', title: '' },
list: [],
list3: [],
list1: [{ text: '口头汇报', moduleId: 11 }, { text: '书面汇报', moduleId: 12 }],
list1Selected: {},
isYellow: false,
showPop: false,
topShow: false,

showTrans: false
};
},
filters: {
Upper: function(value) {
try {
if (value === 1) throw '未评分';
if (value === 2) throw '已评分';
if (value === 3) throw '已过期';
} catch (err) {
return (value = err);
}
},
Upper1: function(value) {
try {
if (value === 1) throw '';
if (value === 2) throw 'yellowB';
if (value === 3) throw 'yellowC';
} catch (err) {
return (value = err);
}
}
},
components: {
XHeader,
ViewBox,
Popup,
Flexbox,
FlexboxItem
},
directives: {
TransferDom
},
methods: {
select(it) {
this.list1Selected = it;
this.showDet();
},
showDet() {
this.topShow = !this.topShow;
this.showTrans = !this.showTrans;
},
clickLink(item) {
if (item.tempint == 1) {
this.$router.push({
path: '/active/detailPack2/',
name: 'detailPack2',
params: {
userId: item.id,
departmentid: item.departmentid,
moduleid: this.list1Selected.moduleId
}
});
} else {
this.$router.push({
path: '/active/detailPack3',
name: 'detailPack3',
params: {
userId: item.id,
departmentid: item.departmentid,
moduleid: this.list1Selected.moduleId
}
});
}
},
change(it){
this.moduleId=it.moduleId;
this.topShow = !this.topShow;
this.showTrans = !this.showTrans;
this.list1Selected = it;
},

getlist1() {
axios({
method: 'get',
url: 'ppartymember/getReportByDepartmentid',
params: {
departmentid: this.$store.getters.user.departmentid,
moudleId: 12
}
})
.then(res => {
this.list = res.data;
console.log('11111111111111', res.data);
})
.catch(function(error) {
console.log(error);
});
},

getlist2() {
axios({
method: 'get',
url: 'ppartymember/getReportByDepartmentid',
params: {
departmentid: this.$store.getters.user.departmentid,
moudleId: 11
}
})
.then(res => {
this.list3 = res.data;
})
.catch(function(error) {
console.log(error);
});
},
know() {
this.showPop = false;
},
datePick(s) {
Date.prototype.toLocaleString = function() {
return this.getFullYear() + '.' + (this.getMonth() + 1) + '.' + this.getDate();
};
return new Date(s).toLocaleString();
},
showMenu() {
this.showPop = true;
}
},
mounted() {
this.list1Selected = this.list1[0];
this.getlist1();
this.getlist2();
}
};



